.form-container {
    position: relative;
    min-height: 100vh;
    background-color: #e1e8ee;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Fira Sans", Helvetica, Arial, sans-serif;
    // font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    .form-structor {
        background-color: #222;
        border-radius: 15px;
        height: 600px;
        width: 500px;
        position: relative;
        overflow: hidden;

        &::after {
            content: "";
            opacity: 0.8;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-repeat: no-repeat;
            background-position: left bottom;
            background-size: 500px;
            background-image: url("https://images.unsplash.com/photo-1503602642458-232111445657?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=bf884ad570b50659c5fa2dc2cfb20ecf&auto=format&fit=crop&w=1000&q=100");
        }

        .signup {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 65%;
            z-index: 5;
            transition: all 0.3s ease;

            &.slide-up {
                top: 5%;
                transform: translate(-50%, 0%);
                transition: all 0.3s ease;
            }

            &.slide-up .form-holder,
            &.slide-up .submit-btn {
                opacity: 0;
                visibility: hidden;
            }

            &.slide-up .form-title {
                font-size: 1em;
                cursor: pointer;
            }

            &.slide-up .form-title span {
                margin-right: 5px;
                opacity: 1;
                visibility: visible;
                transition: all 0.3s ease;
            }

            .form-title {
                color: #fff;
                font-size: 1.7em;
                text-align: center;

                span {
                    color: rgba(0, 0, 0, 0.4);
                    opacity: 0;
                    visibility: hidden;
                    transition: all 0.3s ease;
                }
            }

            .form-holder {
                border-radius: 15px;
                background-color: #fff;
                overflow: hidden;
                margin-top: 50px;
                opacity: 1;
                visibility: visible;
                transition: all 0.3s ease;

                .input {
                    border: 0;
                    outline: none;
                    height: 30px;
                    line-height: 30px;
                    padding: 30px 20px;
                    border-bottom: 1px solid #eee;
                    width: 100%;
                    font-size: 15px;

                    &:last-child {
                        border-bottom: 0;
                    }
                    &::input-placeholder {
                        color: rgba(0, 0, 0, 0.4);
                    }
                }
            }

            .submit-btn {
                background-color: rgba(0, 0, 0, 0.4);
                color: rgba(256, 256, 256, 0.7);
                border: 0;
                border-radius: 15px;
                display: block;
                margin: 15px auto;
                padding: 15px 45px;
                width: 100%;
                font-size: 13px;
                font-weight: bold;
                cursor: pointer;
                opacity: 1;
                visibility: visible;
                transition: all 0.3s ease;

                &:hover {
                    transition: all 0.3s ease;
                    background-color: rgba(0, 0, 0, 0.8);
                }
            }
        }

        .login {
            position: absolute;
            top: 20%;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #fff;
            z-index: 5;
            transition: all 0.3s ease;

            &::before {
                content: "";
                position: absolute;
                left: 50%;
                top: -20px;
                transform: translate(-50%, 0);
                background-color: #fff;
                width: 200%;
                height: 250px;
                border-radius: 50%;
                z-index: 4;
                transition: all 0.3s ease;
            }

            .center {
                position: absolute;
                top: calc(50% - 10%);
                left: 50%;
                transform: translate(-50%, -50%);
                width: 65%;
                z-index: 5;
                transition: all 0.3s ease;

                .form-title {
                    color: #000;
                    font-size: 1.7em;
                    text-align: center;

                    span {
                        color: rgba(0, 0, 0, 0.4);
                        opacity: 0;
                        visibility: hidden;
                        transition: all 0.3s ease;
                    }
                }

                .form-holder {
                    border-radius: 15px;
                    background-color: #fff;
                    border: 1px solid #eee;
                    overflow: hidden;
                    margin-top: 50px;
                    opacity: 1;
                    visibility: visible;
                    transition: all 0.3s ease;

                    .input {
                        border: 0;
                        outline: none;
                        box-shadow: none;
                        display: block;
                        height: 30px;
                        line-height: 30px;
                        padding: 30px 20px;
                        border-bottom: 1px solid #eee;
                        width: 100%;
                        font-size: 15px;

                        &:last-child {
                            border-bottom: 0;
                        }
                        &::input-placeholder {
                            color: rgba(0, 0, 0, 0.4);
                        }
                    }
                }
                .forgot-password {
                    margin: 10px 0;
                    float: right;
                    cursor: pointer;

                    &:hover {
                        color: blueviolet;
                    }
                }

                .submit-btn {
                    background-color: #6b92a4;
                    color: rgba(256, 256, 256, 0.7);
                    border: 0;
                    border-radius: 15px;
                    display: block;
                    margin: 15px auto;
                    padding: 15px 45px;
                    width: 100%;
                    font-size: 13px;
                    font-weight: bold;
                    cursor: pointer;
                    opacity: 1;
                    visibility: visible;
                    transition: all 0.3s ease;

                    &:hover {
                        transition: all 0.3s ease;
                        background-color: rgba(0, 0, 0, 0.8);
                    }
                }

                .social-login {
                    margin-top: 25px;
                    // display: flex;
                    text-align: center;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    h2 {
                        font-size: 20px;
                        font-weight: bold;
                    }

                    .socials {
                        margin-top: 10px;

                        display: flex;
                        justify-content: center;
                        align-items: center;

                        gap: 0 10px;
                        .social {
                            padding: 12px 15px;
                            border-radius: 7px;
                            border: none;

                            background-color: #6b92a4;
                            color: #fff;

                            font-size: 13px;
                            font-weight: normal;

                            cursor: pointer;
                            &:hover {
                                opacity: 0.8;
                            }
                        }
                    }
                }
            }

            &.slide-up {
                top: 90%;
                transition: all 0.3s ease;
            }

            &.slide-up .center {
                top: 10%;
                transform: translate(-50%, 0%);
                transition: all 0.3s ease;
            }

            &.slide-up .form-holder,
            &.slide-up .submit-btn {
                opacity: 0;
                visibility: hidden;
                transition: all 0.3s ease;
            }

            &.slide-up .form-title {
                font-size: 1em;
                margin: 0;
                padding: 0;
                cursor: pointer;
                transition: all 0.3s ease;
            }

            &.slide-up .form-title span {
                margin-right: 5px;
                opacity: 1;
                visibility: visible;
                transition: all 0.3s ease;
            }
        }
    }
}
